.projects {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 0 0.2rem;
}

.projects .project {
  display: flex;
  flex: 1 1 350px;
  position: relative;
  overflow: hidden;
  margin: 1rem;
}

@media (min-width: 800px) {
  .projects .project {
    max-width: 450px;
  }
}

.projects .project .project-image {
  transition: opacity .5s;
}

.projects .project .project-image img {
  width: 100%;
}

.projects .project .project-title {
  position: absolute;
  top: 50%;
  width: 100%;
  font-size: 1.5rem;
  transition: font-size .5s;
  font-weight: 500;
  color: #6d0b41;
  cursor: default;
  display: flex;
  justify-content: center;
}

.projects .project .project-title .project-title-text {
  text-align: center;
  background: rgba(122, 138, 111, 0.7);
}

.projects .project *::-moz-selection {
  background: none;
}

.projects .project *::selection {
  background: none;
}

.projects .project:hover .project-image {
  opacity: .8;
}

.projects .project:hover .project-title {
  font-size: 2rem;
}
